import React from 'react';
import { Container, Typography, Button, Grid } from '@material-ui/core';
import { Link } from 'react-router-dom';
import CartItem from './CartItem/CartItem';
import useStyles from './styles';

const Cart = ({ cart, updateCart, removeCart, emptyCart}) => {

    const classes = useStyles();
    //console.log(cart)


    const EmptyCart = () => (
        <Typography variant="subtitle1">You have no items in your shopping cart,
            start adding some!
            <Link to="/" className={classes.link}>start adding some!</Link>
        </Typography>
    );

    const FilledCart = () => (
        <>
            <Grid container spacing={3}>
                {cart?.line_items?.map((item) => (
                    <Grid item xs={12} sm={4} key={item.id}>
                        <CartItem item={item} onUpdateCartQty={updateCart} onRemoveFromCart={removeCart} />
                    </Grid>
                ))}
            </Grid>
            <div className={classes.cardDetails}>
                <Typography variant="h4">
                    Subtotal: {cart.subtotal.formatted_with_symbol}
                </Typography>
                <div>
                    <Button className={classes.emptyButton} size="large" type="button" variant="contained" color="secondary" onClick={emptyCart}>Empty Cart</Button>
                    <Link to="/checkout">
                        <Button className={classes.checkoutButton} size="large" type="button" variant="contained" color="primary">
                            Checkout
                        </Button>
                    </Link>
                </div>
            </div>
        </>
    );

    if (!cart) return 'Loading...';

    return (
    <Container>
        <div className={classes.toolbar} />
        <Typography className={classes.title} variant="h3">Your Shopping Cart</Typography>
        {(!cart || !cart.line_items || !cart.line_items.length) ? <EmptyCart /> : <FilledCart />}

    </Container>
    )
};

export default Cart;